// Very Small devices - 375px
@mixin xSmall($identifier: "up") {
  @if ($identifier == "down") {
    @media (max-width: #{$screen-xs}) {
      @content;
    }
  } @else {
    @media (min-width: #{$screen-xs}) {
      @content;
    }
  }
}

