@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
.body-container{height:100vh;font-family:"Noto Serif", serif;padding:0 20px}.header{display:flex;flex-wrap:wrap}.hero{width:calc(100% - 100px);height:calc(100% - 50px);margin:10px 50px}@media (min-width: 600px){.hero{width:30%;height:30%}}.name{display:flex;flex-direction:column;font-size:7vw}@media (min-width: 600px){.name{font-size:4vw;justify-content:flex-end}}.contact{display:flex;margin:50px 0}@media (min-width: 600px){.contact{justify-content:center}}.contact .contact-button{display:flex;align-items:flex-end;background-color:#65587e;color:white;padding:7px 14px;border-radius:3px;font-weight:bold}.contact .contact-button .bx-mail-send{font-size:20px;margin-right:5px}.contact .contact-button .contact-email{text-decoration:none;color:snow}.footer{font-family:"Cutive Mono", monospace;display:flex}@media (min-width: 600px){.footer{justify-content:center}}.footer .social{display:flex}*{padding:0;margin:0}

